.containerSemi{
	display: inline-block;
	background-color: rgba(111, 207, 21, 0.5);
	min-width: 19em;
	max-width: 20em;
	margin: 15px;
	border-radius: 14px;
	height: auto;
}

.cardContainer {
	display: inline-block;
	background-color: white;
	margin: 15px;
	min-width: 17em;
	max-width: 18em;
	height: auto;
	border-radius: 14px;
	box-shadow: 0px 1px 4px hsl(0, 7%, 9%);
}

img {
	background-position: 0px 0px;
	background-repeat: no-repeat;
	background-size: contain;
	min-width: 17em;
	max-width: 18em;
	text-align: center;
	border-top-left-radius: 14px;
	border-top-right-radius: 14px;
}

.boldText {
	font-weight: bold;
	font-size: 1.1rem;
	text-align: center;
	padding: 10px 20px 0px 20px;
}

.normalText { 
	font-weight: normal;
	font-size: 0.95rem;
	color: hsl(0, 0%, 50%);
	text-align: center;
	padding-bottom: 10px;
}

.smallerText {
	font-weight: normal;
	font-size: 0.7rem;
	color: hsl(0, 0%, 50%);
	text-align: center;
	letter-spacing: 1px;
	padding-bottom: 20px;
	line-height: 5px;
}

.dataCharters {
	display: flex;
	border-top: solid rgb(206, 206, 206) 1px;
	text-align: center;
}

.specie {
	flex: 1;
}

.gender {
	flex: 1;
}

.state {
	flex: 1;
}