@media only screen and (min-width : 901px) {

.navContainer {
	background-color: rgba(111, 207, 21, 0.5);
	display: inline-block;
    min-width: 78.7em;
	max-width: 78.7em;
    height: 5.1em;
    margin-top: -1%;
}
 
.containerSearch{
    background-color: rgba(111, 207, 21, 0.5);
    min-width: 78.7em;
	max-width: 78.7em;
    height: 4.1em;
}


.searchNav {
    position: absolute; 
    top: 1%; 
    right: 20%;
    width: 20%;
    font-family: 'Open Sans', sans-serif;
    border: 0.2em solid #1b8811;
    padding: 6px;
    height: 36px;
    border-radius: 5px 0px 0px 0px;
    padding-left: 1%; 
}


.botonSearch {
    position: absolute; 
    top: 1%; 
    right: 10%;
    width: 10%;
    height: 36px;
    border: 0.0em solid #1b8811;
    background: #1b8811;
    text-align: center;
    color: rgb(164, 223, 87);
    border-radius: 0px 0px 5px 0px;
    cursor: pointer;
    font-size: 15px;
}
}


@media only screen and (max-width : 900px) {

    .navContainer {
        background-color: rgba(111, 207, 21, 0.5);
        display: inline-block;
        min-width: 30em;
        max-width: 30em;
        height: 5.1em;
        margin-top: -1%;
    }
     
    .containerSearch{
        background-color: rgba(111, 207, 21, 0.5);
        min-width: 30em;
        max-width: 30em;
        height: 4.1em;
    }
    
    
    .searchNav {
        position: absolute; 
        top: 1%; 
        right: 16%;
        width: 20%;
        font-family: 'Open Sans', sans-serif;
        border: 0.2em solid #1b8811;
        padding: 6px;
        height: 16px;
        border-radius: 5px 0px 0px 0px;
        padding-left: 1%; 
    }
    
    
    .botonSearch {
        position: absolute; 
        top: 1%; 
        right: 6%;
        width: 10%;
        height: 32px;
        border: 0.0em solid #1b8811;
        background: #1b8811;
        text-align: center;
        color: rgb(164, 223, 87);
        border-radius: 0px 0px 5px 0px;
        cursor: pointer;
        font-size: 11px;
    }

}